import getTime from "../../../getTime.js";
export default {
	data() {
		return {
			TabCur: 0,
			tabRow: ['当前隐患','历史隐患'],
			startTime: '',
			endTime: '',
			now: '',
			curList: [],
			hisList: [],
			show: false,
			active: 2
		}
	},
	methods: {
		tabSelect(e) {
			this.TabCur = e.currentTarget.dataset.id;
		},
		changetime(e) {
			if (e.target.dataset.time == 'start') {
				this.startTime = e.target.value;
			} else {
				this.endTime = e.target.value;
			}
		},
		getCurList() {
			var self = this;
			uni.request({
				url: self.$store.state.urlPrefix + '/Home/Cur_Problems',
				data: {
					DWCD: self.$store.state.userInfo.DWCD,
					start_time: self.startTime,
					end_time: self.endTime
				}
			}).then(data => {
				var [error, res] = data;
				// console.log(res.data)
				if (res.data == '暂无数据') {
					self.curList = []
				} else {
					let foo = JSON.parse(res.data) 
					self.curList = foo;
					console.log(foo)
				}
			})
		},
		getHisList() {
			var self = this;
			uni.request({
				url: self.$store.state.urlPrefix + '/Home/His_Problems',
				data: {
					DWCD: self.$store.state.userInfo.DWCD,
					start_time: self.startTime,
					end_time: self.endTime,
					Keyword: ''
				}
			}).then(data => {
				var [error, res] = data;
				if (res.data == '暂无数据') {
					self.hisList = []
				} else {
					let foo = JSON.parse(res.data)
					console.log(foo)
					self.hisList = foo;
				}
			})
		},
		SearchMore() {
			uni.showLoading({
				title:"加载中",
				mask: true
			})
			this.getCurList();
			this.getHisList();
			this.$nextTick(() => {
				uni.hideLoading()
			})
		},
		
		showCurDetail(item) {
			uni.navigateTo({
				url: 'searchDangerDetail/searchDangerDetail?YHXH='+item.YHXH + '&No=' + item.todoid + '&state=0'
			})
		},
		showHisDetail(item) {
			uni.navigateTo({
				url: 'searchDangerDetail/searchDangerDetail?YHXH='+item.YHXH + '&No=' + item.todoid + '&state=1'
			})
		},
		linkTo(name) {
			
			switch (name) {
				case 'home':
					this.active = 1;
					this.header = '首页';
					uni.reLaunch({
						url: '/pages/basic/home/home'
					})
					break;
				case 'search':
					this.active = 2;
					this.header = '搜索';
					break;
				case 'todo':
					this.active = 4;
					this.header = '待办事项';
					break;
				case 'my':
					this.active = 5;
					this.header = '个人中心';
					break;
			}
		},
	},
	mounted() {
		this.now = getTime.now();
		uni.showLoading({
			title:"加载中",
			mask: true
		})
		this.getCurList();
		this.getHisList();
		this.$nextTick(() => {
			
			setTimeout(() => {
				uni.hideLoading();
				this.show = true;
			},1000)
		})
	}
}
